@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;height:100vh;width:100%}
.personal-informations{background-color:#44546a;display:flex;flex-direction:column;max-width:20rem;height:100%}.personal-informations .me-picture-holder{width:15rem;margin:.5rem auto;border-radius:50%;box-shadow:rgba(0,0,0,.25) 0px 54px 55px,rgba(0,0,0,.12) 0px -12px 30px,rgba(0,0,0,.12) 0px 4px 6px,rgba(0,0,0,.17) 0px 12px 13px,rgba(0,0,0,.09) 0px -3px 5px;overflow:hidden}.personal-informations .me-picture-holder img{width:100%;height:auto}.personal-informations .arrow-decoration{margin:-3.5rem auto .5rem auto;background-color:#fff;height:13rem;width:100%;-webkit-clip-path:polygon(0 0, 0 15%, 50% 100%, 100% 15%, 100% 0, 50% 80%);clip-path:polygon(0 0, 0 15%, 50% 100%, 100% 15%, 100% 0, 50% 80%)}.personal-informations .about-me{margin:0 1rem auto 1rem;font-family:Arial,Helvetica,sans-serif;color:#fff}.personal-informations .about-me h3{font-family:Arial,Helvetica,sans-serif;font-size:2rem;border-bottom:5px solid #d5dce4}.personal-informations .about-me p{font-size:.9rem;margin:1rem auto;text-align:justify;font-weight:600}.main-informations{display:flex;flex-direction:column;margin:.4rem;width:100%;max-width:45rem}.main-informations .header{width:100%;height:7.5rem;background-color:#44546a;color:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center}.main-informations .header h1{font-family:"Open Sans Condensed",sans-serif;font-size:3rem;text-transform:uppercase;letter-spacing:1px}.main-informations .header h4{font-family:Calibri,"Trebuchet MS",sans-serif;font-size:1.5rem;font-weight:400}.main-informations .cv-content-title{font-family:Calibri,"Trebuchet MS",sans-serif;margin:.5rem auto .5rem 5px;font-size:1.5rem;font-weight:400;text-transform:uppercase;color:#44546a;border-bottom:2px solid #44546a;width:100%;text-align:left}.main-informations .work-experience{display:flex;flex-direction:column}.main-informations .work-experience .company{margin:.3rem auto 0 4rem;font-size:1.2rem;font-weight:400}.main-informations .work-experience .job{margin:0 auto .1rem 8rem}.main-informations .work-experience .firsthand-job{margin:.1rem auto .1rem 8rem;cursor:pointer}.main-informations .work-experience .firsthand-job:visited{color:#181819}.main-informations .technology-skills img{width:4rem;height:auto;margin:0 1rem}.main-informations .technology-skills .development-side{font-family:Calibri,"Trebuchet MS",sans-serif;font-size:1.3rem;font-weight:500;text-transform:uppercase;color:#44546a;width:100%}.main-informations .education{display:flex;flex-direction:column}.main-informations .education .education-date{font-family:Calibri,"Trebuchet MS",sans-serif;font-size:1.4rem;font-weight:500;text-transform:uppercase;color:#44546a;width:100%}.main-informations .education .education-school{font-weight:bold}
